.image_wrapper {
  background-size: cover;
  background-position: center
}

.image_wrapper.ratio_1 {
  height: 0;
  padding-bottom: 100%;
}
.image_wrapper.ratio_2 {
  height: 0;
  padding-bottom: 133%
}
.image_wrapper.ratio_3 {
  height: 0;
  padding-bottom: 166%;
}
.image_wrapper.ratio_4 {
  height: 0;
  padding-bottom: 75%;
}
.image_wrapper.ratio_5 {
  height: 0;
  padding-bottom: 60%;
}

.story-section__title.h1 {
  font-size: 80px;
}

@media screen and (max-width: 1140px) {
  .story-section__title.h1 {
    font-size: 40px;
  }
}

.image-container,
.story-section__title,
.story-section__text {
  position: absolute;
}
.story-section__text {
  font-family: var(--heading-font-small);
}
.story-section__title {
  margin: 0;
}
.story-container {
  padding-left: 5rem;
  padding-right: 5rem;
}
.story-wrapper {
  overflow: hidden;
  max-width: 154rem;
  margin: 0 auto;
}

.text--small {
  width: min(33%, 400px)
}
.text--medium {
  width: min(38%, 540px);
}
.text--big {
  width: min(47.5%, 674px);
}

.animated {
  opacity: 1!important;
}

@media screen and (max-width: 1140px) {
  .story-container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media screen and (max-width: 750px) {
  .text--small,
  .text--medium,
  .text--big {
    width: 80%
  }
}

.video_inner {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding: inherit;

  iframe {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 300%;
    height: 200%;
  }
}

.story-section__title.h1 span {font-size: 6rem;}
.story-section__title.h2 span {font-size: 4rem;}
.story-section__title.h3 span {font-size: 3rem;}

@media only screen and (max-width: 1139px) {
  .story-section__title.h1 span {font-size: 3rem;}
  .story-section__title.h2 span {font-size: 1.4rem;}
  .story-section__title.h3 span {font-size: 1rem;}
}