
.video-gallery-container {
  padding-left: 2rem;
  padding-right: 2rem;
}

.active-video {
  position: relative;
}

.active-player {
  width: 100%;
  height: 100%;
}

.active-video .Icon {
  width: 20px;
  height: 20px;
  color: var(--video-gallery-color);
  fill: var(--video-gallery-color);
  display: block;
}

.active-video .Icon line {
  stroke: var(--video-gallery-color);
  stroke-width: 2px;
}

.active-video .Icon--unmute-outline {
  stroke: var(--video-gallery-color);
}

.control_bar_active-video {
  display: flex;
  align-items: center;
  gap: 1rem;

  position: absolute;
  bottom: 4rem;
  width: calc(100% - 8rem);
  left: 50%;
  transform: translateX(-50%)
}

.progress-bar {
  flex-grow:1; 
}

.timeline {
  color: var(--video-gallery-color);
  letter-spacing: 2px;
}

input[type=range].progress-bar,
input[type=range].volumeRate-bar {
  -ms-appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  cursor: pointer;
}

input[type=range]::-webkit-slider-runnable-track {
  background: var(--video-gallery-color);
  height: 4px;
  -webkit-appearance: none;
  
}

input[type=range]::-moz-range-track {
  background: var(--video-gallery-color);
  height: 3px;
  -webkit-appearance: none;
}

input[type=range]::-ms-track {
  background: var(--video-gallery-color);
  height: 3px;
  -webkit-appearance: none;
}

input[type=range]::-webkit-slider-thumb {
  border: none;
  box-shadow: none;
  height: 10px;
  width: 10px;
  border-radius: 12px;
  background: var(--video-gallery-color);
  -webkit-appearance: none;
  margin: -3px 0 0 0;
  outline: none;
}

input[type=range]::-moz-range-thumb {
  border: none;
  box-shadow: none;
  height: 10px;
  width: 10px;
  border-radius: 12px;
  background: var(--video-gallery-color);
  -webkit-appearance: none;
  margin: -3px 0 0 0;
  outline: none;
}

input[type=range]::-ms-thumb {
  border: none;
  box-shadow: none;
  height: 10px;
  width: 10px;
  border-radius: 12px;
  background: var(--video-gallery-color);
  -webkit-appearance: none;
  margin: -3px 0 0 0;
  outline: none;
}



.active-video .Btn_mute .Icon--unmute-outline {
  display: none;
}
.active-video .Icon--mute-outline {
  display: none;
}
.active-video .Btn_mute .Icon--mute-outline {
  display: block;
}
.active-video .Btn_pause .Icon--play-outline {
  display: none;
}
.active-video .Icon--pause-outline {
  display: none;
}
.active-video .Btn_pause .Icon--pause-outline {
  display: block;
}

.volume-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.player__slider.volumeRate-bar  {
  transform: rotate(270deg) translateX(60%);
  position: absolute;
  top: 0;
}

.player__slider.volumeRate-bar {
  display: none;
}

.rate-active .player__slider.volumeRate-bar {
  display: initial;
}

.active-video .Icon.Icon--play {
  width: 150px;
  height: 150px;
  fill: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.active-video .Icon.Icon--play path {
  fill: var(--video-gallery-color)
}

.active-video .Icon.Icon--play circle {
  stroke: var(--video-gallery-color)
}


.play .control_bar_active-video {
  display: flex;
}

.play .Icon.Icon.Icon--play {
  display: none; 
}

.video-gallery__footer {
  display: grid;
  grid-template-columns: 4fr 6fr;
  gap: 5rem;
  margin-top: 4rem;
}

.video-gallery__slides {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.video-gallery__slide img {
  max-width: 100%;
}

.video-gallery-container h2 {
  margin-top: 0;
}

.video-gallery__slide {
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  background-position: center;
  background-size: cover;
  position: relative;
  cursor: pointer;
}

.video-gallery__slide .Icon.Icon--play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
}

.video-gallery__slide .Icon.Icon--play path {
  fill: var(--video-gallery-color)
}

.video-gallery__slide .Icon.Icon--play circle {
  stroke: var(--video-gallery-color)
}

.active-player {
  background-size: cover;
  background-position: center;
}

.video-gallery__slides.main-carousel {
  padding: 0;
}

.main-wrapper {
  position: relative;
}

.long-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.active-video-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}

.active-video {
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
  overflow: hidden;
}

.main-wrapper {
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
  overflow: hidden;
}

@media screen and (max-width: 750px) {
  .main-wrapper:not(.playing) {
    padding-bottom: 177%;
  }
}

.main-wrapper .long-video {
  display: none;
}

.main-wrapper.playing .long-video {
  display: block;
} 

.long-video-wrapper {
  padding: 0 6rem;
}

.main-wrapper.animated-component {
  padding-left: 10rem;
  padding-right: 10rem;
  transition: padding 1s linear
}

.main-wrapper.animated-component.animated {
  padding-left: 0;
  padding-right: 0;
}

@media screen and (max-width: 750px) {
  .long-video-wrapper {
    padding: 0;
  }
}

.closeBtn_wrapper {
  position: absolute;
  top: 2.5rem;
  right: 2.5rem;
  cursor: pointer;
}

.closeBtn_wrapper .Icon.Icon--close { 
  fill: var(--video-gallery-color);
  color: var(--video-gallery-color);
}

@media screen and (max-width: 980px) {
  .video-gallery__footer {
    grid-template-columns: 1fr;
    gap: 2rem;
    margin-top: 3rem;
  }
}

@media screen and (max-width: 750px) {
  .control_bar_active-video,
  .play .control_bar_active-video {
    display: none;
  }

  .active-video .Icon.Icon--play {
    width: 80px;
    height: 80px;
  }
  
  .video-gallery__slides {
    display: flex;
    
  }

  .video-gallery__slides .carousel-cell {
    width: 70%;
    margin-right: 2rem;
    padding-bottom: 39.375%;
  }
  
}
