.section-container-small {
  margin-left: 12rem;
  margin-right: 12rem;
}

.section-container-big-stretch {
  margin-left: 0;
  margin-right: 0;
}

.section-wrapper {
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
  overflow: hidden;

  background-size: cover;
  background-position: center;
}

.section-wrapper::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.section-container-small .section-wrapper {
  padding-bottom: 56.25%;
}

.section-container-full .section-wrapper {
  padding-bottom: 100vh;
}

.section-wrapper video { 
  position: absolute; 
  top: 0; 
  left: 50%;
  z-index: 1;
  width: 200%;
  height: 100%;
  transform: translateX(-50%);
  background-size: cover;
  background-position: center;
}

.section-container-full .section-wrapper video {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  width: max(200vh, 250%);
  height: max(100vh, 250%);
  transform: translate(-50%, -50%);
}

.image-video-content {
  position: absolute;
  z-index: 2;
  width: 100%;  
  display: flex;
  flex-direction: column;
}

.image-video-content .text-small,
.image-video-content .text,
.image-video-content .text-large {
  max-width: 65%;
}

.image-video-content h2,
.image-video-content .text-small,
.image-video-content .text,
.image-video-content .text-large {
  color: #fff;
  margin-top: 0.8rem;
  margin-bottom: 0.8rem;
}

.image-video-content--middleLeft {
  top: 50%;
  left: 5rem;
  transform: translateY(-50%);
  align-items: flex-start;
  text-align: start;
  max-width: 50%;
}

.image-video-content--middleCenter {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  align-items: center;
  text-align: center;
}
.image-video-content--middleRight {
  top: 50%;
  right: 5rem;
  transform: translateY(-50%);
  align-items: flex-end;
  text-align: end;
  max-width: 50%;
}

.image-video-content--bottomLeft {
  bottom: 8rem;
  left: 5rem;
  align-items: flex-start;
  text-align: start;
  max-width: 50%;
}

.image-video-content--bottomCenter {
  bottom: 8rem;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
  text-align: center; 
}

.image-video-content--bottomRight {
  bottom: 8rem;
  right: 5rem;
  align-items: flex-end;
  text-align: end;
  max-width: 50%;
}

@media screen and (max-width: 750px) {
  .image-video-content--middleLeft {
    left: 2.5rem;
    width: calc(100% - 5rem);
    max-width: none;
  }
  .image-video-content--bottomLeft {
    left: 2.5rem;
    bottom: 4.5rem;
    width: calc(100% - 5rem);
    max-width: none;
  }

  .image-video-content--middleRight {
    right: 3rem;
    width: calc(100% - 6rem);
    max-width: none;
  }
  .image-video-content--bottomRight {
    right: 3rem;
    bottom: 4.5rem;
    width: calc(100% - 6rem);
    max-width: none;
  }
  .image-video-content--bottomCenter {
    bottom: 4.5rem;
    width: calc(100% - 4rem)
  }

  .image-video-content--middleCenter {
    width: calc(100% - 4rem)
  }
}


/* Video Controllers */

.ButtonGroup {
  display: flex;
  gap: 2.4rem;
  margin-top: 2rem;
}

.ButtonGroup--controlers {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 4rem;
  z-index: 2;
  justify-content: space-between;
}

.ButtonGroup--left {
  left: 4rem;
}

.ButtonGroup--center {
  left: 4rem;
  right: 4rem;
}

.ButtonGroup--right {
  right: 4rem;
}

.ButtonGroup--controlers button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.ButtonGroup--controlers svg {
  width: 25px;
  height: 25px;
  color: #fff;
  fill: #fff;
  stroke: #fff;
}

.ButtonGroup--controlers svg[data-type = resume] {
  display: none;
}

.ButtonGroup--controlers.Btn_pause svg[data-type = resume] {
  display: initial;
}

.ButtonGroup--controlers.Btn_pause svg[data-type = pause] {
  display: none;
}

.ButtonGroup--controlers svg[data-type = unmute] {
  display: none;
}

.ButtonGroup--controlers.Btn_unmute svg[data-type = unmute] {
  display: initial;
}

.ButtonGroup--controlers.Btn_unmute svg[data-type = mute] {
  display: none;
}

/* Media querries */

@media screen and (max-width: 750px) {
  .section-container-small {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .section-container-big {
    margin-left: 0;
    margin-right: 0;
  }

  .section-container-big-stretch {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .section-container-full {
    margin-left: 0;
    margin-right: 0;
  }

  .section-container-big .section-wrapper,
  .section-container-big-stretch .section-wrapper
   {
    height: auto;
    padding-bottom: 177%;
  }

  .section-container-small .section-wrapper {
    padding-bottom: 166%;
  }

  .section-wrapper video {
    top: 1px; 
    left: 50%;
    transform: translateX(-50%);
    width: max(350%, 250vh);  
  }

  .ButtonGroup--controlers {
    bottom: 2rem;
  }

  .ButtonGroup--left {
    left: 1.5rem;
  }

  .ButtonGroup--right {
    right: 1.5rem
  }

  .ButtonGroup--center {
    left: 1.5rem;
    right: 1.5rem
  }

  .section-container {
    padding: 4rem 2rem;
  }

  .section-container-full {
    margin: 0;
  }

  .image-video-content .Button:not(:first-child) {
    display: none;
  }
}



@media screen and (max-width: 850px) {
  .image-video-content .text-small,
  .image-video-content .text,
  .image-video-content .text-large {
    max-width: 85%;
  }
  .ButtonGroup {
    margin-top: 1rem  
  }
} 

@media screen and (max-width: 500px) {
  .ButtonGroup {
    flex-direction: column;
    gap: 1.6rem;
  }
} 